export const FareCalculator = (distance,time,rateDetails,instructionData, decimal)=>{  

    let baseCalculated =  (parseFloat(distance) +  (parseFloat(time) / 3600)) * 1.5;

    if(baseCalculated < 250){
        baseCalculated = 250
    }

    const boxPrice = baseCalculated
    let discount = 0
    const totalBoxesPrice = boxPrice * instructionData.numberOfBoxes
    if(instructionData.numberOfBoxes > 1 && instructionData.numberOfBoxes < 7) {
        discount = 0.05 * instructionData.numberOfBoxes
    }else if(instructionData.numberOfBoxes > 6){
        discount = 0.05 * 6
    }

    let total = totalBoxesPrice - (discount * totalBoxesPrice)

    const convenienceFee = 0
    let grand = total + convenienceFee;

    console.log("------fare calculator-----",{distance:parseFloat(distance),boxPrice,discount,totalBoxesPrice,total,grand})

    return {
        totalCost:parseFloat(total.toFixed(decimal)),
        grandTotal:parseFloat(grand.toFixed(decimal)),
        convenience_fees:parseFloat(convenienceFee.toFixed(decimal))
    }
     
}
